export default {
  props: {
    scaleX: {
      type: Function,
      required: true
    },
    scaleY: {
      type: Function,
      required: true
    },
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  }
}



